export const Portfolio = React.memo(({onDone}) => {
	const [portfolio, setPorfolio] = useState('1');
	const portRef = useRef(null);
	
	const onKey = useCallback(e => {
		if(e.code === "Escape")
			onDone();
		
	}, [onDone]);
	
	useEffect(() => {
		if(portRef.current != null)
			portRef.current.scrollTop = 0;
	}, [portfolio]);
	
	useOnKeyDown(onKey);
	
	const optionChange = useCallback((v) => {
		setPorfolio(v);
	}, []);
	
	return (<>
			<p>Press <u onClick={onDone}>[ESC]</u> to go back.</p>
			<div className="contentBody d-flex border-up">
				<div className="options">
					<OptionMenu options={latestProjects} onChange={optionChange}/>
				</div>
				<div className="portfolio" ref={portRef}>
					{ portfolio === '1' && <ProjectOne/> }
					{ portfolio === '2' && <ProjectTwo/> }
					{ portfolio === '3' && <ProjectThree/> }
					{ portfolio === '4' && <ProjectFour/> }
					{ portfolio === '5' && <ProjectFive/> }
				</div>
			</div>
		</>
	);
});