import React, { useCallback, useMemo, useEffect, useState } from "react";
import Typist from "react-typist";
import { Contact } from "./Components/Contact";
import { Resume } from "./Components/Resume";
import { Portfolio } from "./Components/Portfolio";
import { Chaos } from "./Components/Chaos";
import { OptionMenu } from "./Components/OptionMenu";
import useOnKeyDown from "./Hooks/useOnKeyDown";
import GM from "./GM.png";

const machineDelay = () => 10;

const firstOptions = [
  {
    value: "1",
    text: (
      <span>
        Contact <span className="userChoice">Gabriel Martinez</span>
      </span>
    ),
  },
  {
    value: "2",
    text: (
      <span>
        View some of <span className="userChoice">Gabriel Martinez</span>'s
        latest projects
      </span>
    ),
  },
  {
    value: "3",
    text: (
      <span>
        Read <span className="userChoice">Gabriel Martinez</span>'s resum√©
      </span>
    ),
  },
];

const App = React.memo(function () {
  const [normal, setNormal] = useState(true);
  const [isBooting, setIsBooting] = useState(true);
  const [hasBooted, setHasBooted] = useState(false);
  const [nextKey, setNextKey] = useState(null);
  const [whatToDo, setWhatToDo] = useState(null);
  const [codeIndex, setCodeIndex] = useState(0);

  useEffect(() => {
    if (nextKey == null) return;

    let eventSuccess = false;
    switch (codeIndex) {
      case 0:
      case 1:
        if (nextKey === "ArrowUp" || nextKey === "KeyU") {
          setCodeIndex(codeIndex + 1);
          eventSuccess = true;
        }
        break;
      case 2:
      case 3:
        if (nextKey === "ArrowDown" || nextKey === "KeyD") {
          setCodeIndex(codeIndex + 1);
          eventSuccess = true;
        }
        break;
      case 4:
      case 6:
        if (nextKey === "ArrowLeft" || nextKey === "KeyL") {
          setCodeIndex(codeIndex + 1);
          eventSuccess = true;
        }
        break;
      case 5:
      case 7:
        if (nextKey === "ArrowRight" || nextKey === "KeyR") {
          setCodeIndex(codeIndex + 1);
          eventSuccess = true;
        }
        break;
      case 8:
        if (nextKey === "KeyA") {
          setCodeIndex(codeIndex + 1);
          eventSuccess = true;
        }
        break;
      case 9:
        if (nextKey === "KeyB") {
          setNormal(false);
          setTimeout(() => setNormal(true), 31000);
        }
        break;
      default:
        break;
    }

    if (!eventSuccess && codeIndex > 1) {
      setCodeIndex(0);
    }
    setNextKey(null);
  }, [nextKey, codeIndex]);

  const onKey = useCallback((e) => {
    setNextKey(e.code);
  }, []);

  useOnKeyDown(onKey);

  useEffect(() => {
    setTimeout(() => {
      setIsBooting(false);
      setTimeout(() => setHasBooted(true), 1000);
    }, 5000);
  }, []);

  /*const [firstRow, secondRow, thirdRow] = useMemo(() => {
		let f ='qwertyuiop'.split(''); 
		let s = 'asdfghjkl'.split('');
		let t = '@zxcvbnm.'.split('');
		
		const turner = letter => <Key key={letter} letter={letter}/>;
		
		return [f.map(turner), s.map(turner), t.map(turner)];
	}, []);*/

  const onWhatToDO = useCallback((v) => {
    setWhatToDo(v);
  }, []);

  const goBack = useCallback(() => {
    setWhatToDo(null);
  }, []);

  const currentScreen = useMemo(() => {
    switch (whatToDo) {
      case 1:
      case "1":
        return <Contact onDone={goBack} />;
      case 2:
      case "2":
        return <Portfolio onDone={goBack} />;
      case 3:
      case "3":
        return <Resume onDone={goBack} />;
      case null:
      default:
        return (
          <div className="contentBody">
            This website belongs to{" "}
            <span className="userChoice">Gabriel Martinez</span>'s (Senior
            Fullstack software engineer), what do you wish to do? (available
            input devices: keyboard, point and click)
            <OptionMenu options={firstOptions} onSubmit={onWhatToDO} />
          </div>
        );
    }
  }, [goBack, onWhatToDO, whatToDo]);

  return (
    <div className="App">
      <div className="screen">
        {normal ? (
          <>
            <div className="sun"></div>
            <div className="sunLine"></div>
            <div
              className="bg"
              style={{ height: Math.pow(7, codeIndex) }}
            ></div>
            <div className="glow"></div>
            <div className="content">
              <p className="title">
                {" "}
                {hasBooted && <img alt="logo" className="logo" src={GM} />}{" "}
                Virtual Business Card Assistant 2000 [v0.9.10]
              </p>
              {isBooting && (
                <div className="contentBody">
                  Whose record do you wish to pull?
                  <Typist
                    cursor={{
                      blink: true,
                      element: "‚ñà",
                      hideWhenDone: true,
                      hideWhenDoneDelay: 0,
                    }}
                  >
                    <Typist.Delay ms={500} />
                    <b> Gabriel Martinez</b>
                  </Typist>
                  <Typist
                    cursor={{ show: false }}
                    delayGenerator={machineDelay}
                  >
                    <Typist.Delay ms={2000} />
                    <br />
                    Loading:{" "}
                    <span className="userChoice">Gabriel Martinez</span>'s
                    personal info ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..-
                    ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-..
                    .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .-
                    -... ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -..
                    -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-..
                    .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..-
                    ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-..
                    .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .-
                    -... ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -..
                    -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-..
                    .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..-
                    ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-..
                    .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .-
                    -... ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -..
                    -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-..
                    .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .- -... ..-
                    ..- -.. -.. .-.. .-. .-.. .-. .- -... ..- ..- -.. -.. .-..
                    .-. .-.. .-. .- -... ..- ..- -.. -.. .-.. .-. .-.. .-. .-
                    -...
                  </Typist>
                </div>
              )}
              {hasBooted && currentScreen}
            </div>
            <div className="scanline"></div>
            <div className="scanlines"></div>
            <div className="scanlinesV"></div>
          </>
        ) : (
          <Chaos />
        )}
      </div>
    </div>
  );
});

export default App;
