import React, { useCallback, useEffect, useState } from 'react';
import useOnKeyDown from '../Hooks/useOnKeyDown';

const Option = React.memo(({value, text, onClick, selected}) => {
	return <div onClick={onClick} className="option"> { value } ) [{selected ? '‚ñ†' : <span className="hiddenText">‚ñ†</span>}] { text } </div>
});

export const OptionMenu = React.memo(({options, onChange, onSubmit}) => {
	
	const [currentVal, setCurrentVal] = useState(() => options[0].value);
	
	useEffect(() => {
		if(onChange != null && onChange !== undefined)
			onChange(currentVal);
	}, [ currentVal, onChange ]);
	
	const onKey = useCallback(e => {
		
		let currentIndex = 0;
		let newIndex = 0;
		const vals = [];
		
		for(let opt = 0, l = options.length; opt < l; ++opt){
			vals.push(options[opt].value);
			
			if(options[opt].value === currentVal)
				currentIndex = opt;
		}
		
		switch(e.code){
			case "Enter":
				if(onSubmit != null && onSubmit !== undefined)
					onSubmit(currentVal);
				break;
			case "ArrowUp":
				newIndex = currentIndex - 1;
				
				if(newIndex < 0)
					newIndex = options.length - 1;
				
				setCurrentVal(options[newIndex].value);
				break;
			case "ArrowDown":
				newIndex = currentIndex + 1;
				
				if(newIndex >= options.length)
					newIndex = 0;
				
				setCurrentVal(options[newIndex].value);
				break;
			default:
				let val = e.key;
				newIndex = vals.indexOf(val);
				if(newIndex >= 0)
					setCurrentVal(options[newIndex].value);
				break;
		}
		
	}, [currentVal, options, onSubmit]);
	
	useOnKeyDown(onKey);
	
	const onOptClick = useCallback(v => {
		setCurrentVal(v);
		if(onSubmit != null && onSubmit !== undefined)
			onSubmit(v);
	}, [onSubmit]);
	
	return options.map(option => <Option onClick={e => onOptClick(option.value)} key={option.value} value={option.value} text={option.text} selected={currentVal === option.value}/>);
});