import React, { useCallback, useEffect, useState } from 'react';
import Typist from 'react-typist';

const repeat = ['repeat', 'repeat-x', 'repeat-y', 'no-repeat'];
const positionX = ['left', 'right', 'center'];
const positionY = ['top', 'bottom', 'center'];
const sizes = ['contain', 'cover', '100px', '500px', '250px', '50px'];
const t = "ÔªöÔª∑ÔªªÔ¥æÔ¨ÜÔ≠èÔ≠âÔ¨ªÍ≠°Íû´Ô∫®ÍùåÍùèÍû≥ÍùòÍùÑ‚±ø‚Ç©‚ÇØ‚Ç¥‚Ç™‚ÇÆ‚Çø‚Ñ¶‚Çæ‚Ç≤‚ÄΩ·ø∫·æ∂·µ∫·µ∑·µ≤·¥Ç·¥É€û·µµ€ù€©◊êŸ£◊ë◊ì◊ñ◊õ◊™◊¶◊£◊ß◊ù◊û÷è÷é‘™“à“â“ä–ë—™ Ü ò í‚ôÑ‚ôÇ‚òâ‚ôÜ‚ôÉ‚òø‚ö∏‚ôÖ‚õ¢‚ôÄüúÅüúÇüúÉüúÑüúãüúçüúîüúïüú™üú≥üùéüù™".split('');

const getNewText = () => {
	let newText = [];
	for(let i = 0; i < 4000; i++){
		newText.push(t[ Math.floor( Math.random() * 83 ) ]);
	}
	
	return newText.join(' ');
};

export const Chaos = React.memo(p => {
	
	const [showKats, setShowKats] = useState(false);
	const [hasBooted, setBooted] = useState(false);
	const [rot, setRot] = useState(0);
	const [render, setRender] = useState(0);
	const [r, setR] = useState('no-repeat');
	const [x, setX] = useState('center');
	const [y, setY] = useState('center');
	const [s, setS] = useState('contain');
	const [text, setText] = useState(getNewText);
	
	useEffect(() => {
		setTimeout(() => { 
			setBooted(true);
			setTimeout(() => { setShowKats(true) }, 15000);
		}, 10000);
	}, [])
	
	useEffect(() => {
		if(!hasBooted)
			return; 
		
		setR(repeat[ Math.floor( Math.random() * 4 ) ]);
		setX(positionX[ Math.floor( Math.random() * 3 ) ]);
		setY(positionY[ Math.floor( Math.random() * 3 ) ]);
		setS(sizes[ Math.floor( Math.random() * 5 ) ]);
		setTimeout(() => setRender(render + 1), 66);
		setText(getNewText);
	}, [hasBooted, render]);
	
	const rotate = useCallback(() => {
		setRot(rot + 45);
	}, [rot]);
	
	return (showKats ? <div className="kats" onClick={rotate} style={ {transform : `rotateZ(${rot}deg)`} }></div>: <>
		{ hasBooted && <div className="rdd"></div> }
		<div className="chaosBG" style={ { backgroundRepeat : r, backgroundPosition : `${x} ${y}`, backgroundSize : s } }></div>
		<div className="glow"></div>
		<div className={`content ${ hasBooted ? ' blinking' : ''}`}>
			{ hasBooted ? text : <Typist cursor={ {show : false} } >
				<Typist.Delay ms={1000}/>
				{ text }
			</Typist> }
		</div>
		<div className="scanline"></div>
		<div className="scanlines"></div>
		{ hasBooted && <div className="blkk"></div> }
	</>);
	/**/
});
